@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search{
  margin-bottom: 64px;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.coin-text{
  margin-bottom: 32px;
  text-align: center;
}
.coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border:none;
  background: rgb(66,51,57);
  background: linear-gradient(90deg, rgba(66,51,57,1) 0%, rgba(60,66,73,1) 100%);
  color:#e2e2e2;
}

.coin-input::-webkit-input-placeholder{
  color:#e2e2e2;
}

.coin-input:-ms-input-placeholder{
  color:#e2e2e2;
}

.coin-input::placeholder{
  color:#e2e2e2;
}
.coin-container{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.coin-row{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 1115px;
}

.coin{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  padding-right: 24px;
  min-width: 300px;
}

.coin h1 {
  font-size: 18px;
  width: 150px;
}

.coin img{
  height: 40px;
  width: 40px;
  margin-right: 10px;
}

.coin-symbol{
  text-transform: uppercase;
}

.coin-data{
  display: -webkit-flex;
  display: flex;
  text-align: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
}
.coin-graph{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  padding-left:10px ;
}
.coin-price{
  width:110px
}
.coin-volume{
  width: 200px;
}
.coin-marketcap{
  width: 240px;
}
.coin-percent{
  width: 80px;
}
.red{
  color: #f00606
}
.green{
  color: #11d811;
}
.header-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.header-row{
  display: -webkit-flex;
